/* ---------------- Common Start ---------------- */

.center {
    text-align: center;
    justify-content: center;
}

.pageContainer {
    position: relative;
    display: block;
}

.pagePadding {
    padding-top: 15px;
    padding-bottom: 15px;
}

.title {
    padding: 10px 0px;
    margin: 0;
    font-weight: bold;
}

.title::after {
    content: " ";
    display: block;
    background-color: #333;
    height: 2px;
    width: 20%;
    min-width: 150px;
    margin: 10px auto;
}

.content {
    font-weight: bold;
}

@media screen and (min-width: 751px) {
    .pagePadding {
        padding-left: 10%;
        padding-right: 10%;
    }
    .title {
        font-size: 40px;
    }
    .content {
        font-size: 20px;
    }
}

@media screen and (max-width: 750px) {
    .pagePadding {
        padding-left: 10px;
        padding-right: 10px;
    }
    .title {
        font-size: 20px;
    }
    .content {
        font-size: 15px;
    }
}

/* ---------------- Common End ---------------- */

/* ---------------- Home Page Start ---------------- */

.homeBackground {
    height: 100%;
    display: flex;
    background-repeat: no-repeat;
    background-color: transparent;
    background-position: center bottom;
    background-size: cover;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
}

.homeTitleContainer {
    margin-bottom: auto;
    margin-left: auto;
    margin-right: auto;
}

.homeTitleH1 {
    color: #fff;
    margin-top: 30px;
    margin-bottom: 30px;
    font-style: italic;
}

.homeTitleH2 {
    color: #fff;
    margin: 0;
}

.homeTitleH3 {
    display: grid;
    width: 100%;
    grid-gap: 20px;
    grid-template-columns: minmax(20px, 1fr) auto;
    align-items: center;
    color: #fff;
    margin: 0;
}

.homeTitleH3::before {
    content: " ";
    background-color: #fff;
    height: 2px;
    flex: 1 0 20px;
    box-shadow: 3px 3px 0 #000, -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}

@media screen and (min-width: 751px) {
    .homeTitleContainer {
        margin-top: 25vh;
    }
    .homeTitleH1 {
        font-size: 120px;
        text-shadow: 6px 6px 0 #000, -2px -2px 0 #000, 2px -2px 0 #000, -2px 2px 0 #000, 2px 2px 0 #000;
        /* -webkit-text-stroke: 1px black; */
    }
    .homeTitleH2 {
        font-size: 60px;
        text-shadow: 3px 3px 0 #000, -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
    }
    .homeTitleH3 {
        font-size: 40px;
        text-shadow: 3px 3px 0 #000, -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
    }
}

@media screen and (max-width: 750px) {
    .homeTitleContainer {
        margin-top: 30vh;
    }
    .homeTitleH1 {
        font-size: 60px;
        text-shadow: 4.5px 4.5px 0 #000, -1.5px -1.5px 0 #000, 1.5px -1.5px 0 #000, -1.5px 1.5px 0 #000, 1.5px 1.5px 0 #000;
    }
    .homeTitleH2 {
        font-size: 25px;
        text-shadow: 3px 3px 0 #000, -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
    }
    .homeTitleH3 {
        font-size: 20px;
        text-shadow: 3px 3px 0 #000, -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
    }
}

/* ---------------- Home Page End ---------------- */

/* ---------------- AboutMe Page Start ---------------- */

.aboutMeBackground {
    background-color: #f0f0f0;
    text-align: center;
}

.aboutMeList {
    list-style-type: none;
    padding: 0px;
}

.aboutMeList li {
    margin: 8px 0px;
}

.svgContainer {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 25vw;
    min-height: 200px;
    max-height: 400px;
}

.gradHatSVG {
    position: absolute;
    display: inline-block;
    margin: 0 auto;
    height: 100%;
    opacity: 1;
    -webkit-animation: gradHatRotateY 10s ease-in-out infinite;
    animation: gradHatRotateY 10s ease-in-out infinite;
}

@-webkit-keyframes gradHatRotateY {
    0% {
        transform: rotateY(0);
        opacity: 1;
    }
    40% {
        transform: rotateY(0);
        opacity: 1;
    }
    50% {
        transform: rotateY(360deg);
        opacity: 0;
    }
    90% {
        transform: rotateY(360deg);
        opacity: 0;
    }
    100% {
        transform: rotateY(0);
        opacity: 1;
    }
}

@keyframes gradHatRotateY {
    0% {
        transform: rotateY(0);
        opacity: 1;
    }
    40% {
        transform: rotateY(0);
        opacity: 1;
    }
    50% {
        transform: rotateY(360deg);
        opacity: 0;
    }
    90% {
        transform: rotateY(360deg);
        opacity: 0;
    }
    100% {
        transform: rotateY(0);
        opacity: 1;
    }
}

.hkustSVG {
    position: absolute;
    display: inline-block;
    margin: 0 auto;
    height: 100%;
    opacity: 0;
    -webkit-animation: hkustRotateY 10s ease-in-out infinite;
    animation: hkustRotateY 10s ease-in-out infinite;
}

@-webkit-keyframes hkustRotateY {
    0% {
        transform: rotateY(0);
        opacity: 0;
    }
    40% {
        transform: rotateY(0);
        opacity: 0;
    }
    50% {
        transform: rotateY(360deg);
        opacity: 1;
    }
    90% {
        transform: rotateY(360deg);
        opacity: 1;
    }
    100% {
        transform: rotateY(0);
        opacity: 0;
    }
}

@keyframes hkustRotateY {
    0% {
        transform: rotateY(0);
        opacity: 0;
    }
    40% {
        transform: rotateY(0);
        opacity: 0;
    }
    50% {
        transform: rotateY(360deg);
        opacity: 1;
    }
    90% {
        transform: rotateY(360deg);
        opacity: 1;
    }
    100% {
        transform: rotateY(0);
        opacity: 0;
    }
}

.aboutMeList {
    margin: 0;
    padding: 10px 0px;
}

.aboutMeList>li {
    line-height: 1.5;
}

/* ---------------- AboutMe Page End ---------------- */

/* ---------------- Project Page Start ---------------- */

.flex-container {
    display: flex;
    flex-direction: row;
}

@media (max-width: 800px) {
    .flex-container {
        flex-direction: column;
    }
}

.project-card-new {
    width: 45%;
    max-width: 800px;
    margin: 16px auto;
    border: 3px black solid;
}

@media (max-width: 800px) {
    .project-card-new {
        width: 80%;
    }
}

.project-card-image {
    flex: 4;
    min-height: 200px;
    min-width: 200px;
    overflow: hidden;
    background-color: rgb(200, 200, 200);
}

.project-card-image img::before {
    display: block;
    padding-bottom: 100%;
}

.project-card-image img {
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;
    transition: transform 250ms;
}

.project-card-image img:hover {
    transform: scale(1.25);
  }

.project-card-text {
    flex: 6;
    min-height: 160px;
    margin: 20px;
    display: flex;
    flex-direction: column;
    text-align: start;
    align-items: flex-start
}

.project-card-text h1 {
    font-size: 24px;
    margin: 0px 0px 16px 0px;
}

.project-card-text p {
    margin: 0px;
}

.project-card-text a {
    margin-top: auto;
    align-self: flex-end;
    min-width: 100px;
}

@media (max-width: 800px) {
    .project-card-text a {
        margin-top: 32px;
    }
}

.button {
  background-color: #405cf5;
  border-radius: 8px;
  border-style: none;
  box-sizing: border-box;
  color: #FFFFFF;
  cursor: pointer;
  display: inline-block;
  font-family: "Haas Grot Text R Web", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 14px;
  font-weight: 500;
  height: 40px;
  line-height: 20px;
  list-style: none;
  margin: 0;
  outline: none;
  padding: 10px 16px;
  position: relative;
  text-align: center;
  text-decoration: none;
  transition: color 100ms;
  vertical-align: baseline;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.button:hover,
.button:focus {
  background-color: #566ef5;
}

.projectBackground {
    background-color: #ffffff;
    text-align: center;
}

.projectCard {
    display: inline-block;
    position: relative;
    width: 80%;
    max-width: 400px;
    margin: 30px auto;
    perspective: 1000px;
}

.projectDummy {
    padding-top: 100%;
}

.projectContent {
    height: 100%;
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
    transition: transform 1s;
    -webkit-transition: transform 1s;
    transform-style: preserve-3d;
}

.projectCard:hover .projectContent {
    transform: rotateY(180deg);
    transition: transform 0.5s;
}

.front, .back {
    padding: 0;
    margin: 0;
    text-align: center;
    border-radius: 5px;
    backface-visibility: hidden;
    -webkit-backface-visibility: hidden;
}

.back {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: #03446A;
    color: white;
    transform: rotateY(180deg);
}

.back a {
    position: relative;
    display: block;
    color: #60ffff;
    text-decoration: none;
}

.back a:before, .back a:after {
    content: " ";
    display: inline-block;
    background-color: #60ffff;
    height: 2px;
    width: 10%;
    position: absolute;
    top: 50%;
}

.back a:before {
    transform: translate(-120%, -50%)
}

.back a:after {
    transform: translate(20%, -50%)
}

.projectImg {
    width: 100%;
}

/* ---------------- Project Page End ---------------- */

/* ---------------- Contact Page Start ---------------- */

.contactBackground {
    background-color: #f0f0f0;
    text-align: center;
}

.contactWay {
    position: relative;
    display: inline-block;
    margin: 10px;
    width: 18%;
    max-width: 120px;
}

.contactWay a {
    display: block;
    height: 100%;
    cursor: pointer;
}

.contactSVG {
    pointer-events: none;
    width: 100%;
}

.copyright {
    padding-top: 50px;
}

@media screen and (min-width: 751px) {
    .copyright {
        font-size: 16px;
    }
}

@media screen and (max-width: 750px) {
    .copyright {
        font-size: 8px;
    }
}